                <span class="iconfont" :class="(key == helpListShowIndex && rotate == true)?'rotate':''">&#xe6bd;</span>
              </div>
              <div class="help-list" v-show="key == helpListShowIndex">
                <ul>
                  <!--<li v-on:click="hideHelpList" class="hide-list">收起</li>-->
                  <li class="item" v-for="(j,index) in helpListData">
