<script>
  import axios from 'axios'
  import qs from 'qs'
  import share from '../WxShare'
  export default {
    name: "ShetuanDream",
    data() {
      return {
        inProgressData: [],//正在进行
        completeData: [],//已经完成
        helpListData: [],//帮助列表
        helpListShowIndex:null,//显示当前点击的帮助列表
        loadingShow:true,
        start:0,
        helpListStart:0,
        limit:10,
        status:1,
        noMore:false,//查看更多
        noMoreHelp:true,//帮助列表查看更多
        noContent:false,//无内容时
        tab:true,//切换进行中已完成
        currentKey:'',//helpLiist的key值，第一次点击加载，第二次点击收起
        searchWord:'',//搜索内容
        rotate:false,//旋转样式
        searchPage:false,//搜索成功后显示的页面
      }
    },
    created() {
     this.choiceTab('1');
     share.ready('http://dashi.baidu.com/campuswx/shetuan.html#/',
       'http://campusweixin.bj.bcebos.com/1_base/1d4603365aa2a5d77290d6474970fc8a.PNG',
       '百度校园·最青春·社团圆梦',
       '无论你的社团是缺服装、缺妆发还是缺设备…只要你们有梦想、有凝聚力、想做出改变，百度校园就能帮你实现！')
    },
    methods: {
      choiceTab(str) {
        this.helpListShowIndex = null;
        this.rotate = false;
        this.searchPage =false;
        if (str == '1') {
          this.tab = true;
          this.getPageData(null,1)
        } else {
          this.tab = false;
          this.getPageData(null,2)
        }
      },
      getPageData(str,status){
        this.loadingShow = true;
        if(str == 'more'){
          this.start = this.start + 10;
        }else{
          this.start = 0;
          this.status = status;
        }
        let data = {
          start:this.start,
          limit:this.limit,
          status:this.status
        }
        axios.post(this.pc_proxy + 'queryDreams.do',qs.stringify(data)).then(res => {
          if (res.status == 200 && res.data.error_no == '0') {
            let data = res.data.data.datas
            let total = res.data.data.total;
            if(str == 'more'){
              this.inProgressData = this.inProgressData.concat(data);
              total = total - 10;
              if(total <= 10){
                this.noMore = false
              }else{
                this.noMore = true;
              }
            }else{
              this.inProgressData = data;
              if(total > 10){
                this.noMore = true
              }else{
                this.noMore = false;
              }
            }
            this.loadingShow = false;
            if(data.length <= 10){
              if(data.length == 0){
                if(str !== 'more'){
                  this.noContent = true;
                }
              }else{
                this.noContent = false;
              }
            }
          }
        })
      },
      showHelpList(key,id,flag){
        if(this.currentKey === key && flag !== 'more'){
          this.helpListShowIndex = null;
          this.currentKey = '';
          this.rotate = false;
        }else{
          if(flag == 'start'){
            this.helpListStart = 0;
          }
          this.loadingShow = true;
          axios.post(this.pc_proxy + 'queryDreamsUsersById.do?id='+ id +'&start='+ this.helpListStart + '&limit=10').then(res => {
            if (res.status == 200 && res.data.error_no == '0') {
              this.currentKey = key;
              this.helpListShowIndex = key;
              // this.helpListStart += 10;
              if(flag == 'more'){
                this.helpListData = this.helpListData.concat(res.data.data)
              }else{
                this.helpListData = res.data.data;
              }
              if(res.data.data.length < 10){
                this.noMoreHelp = false;
              }else{
                this.noMoreHelp = true;
              }
              this.loadingShow = false;
              this.rotate = true;
            }
          })
        }
      },
      showMoreHelpList(key,id){
        this.helpListStart += 10;
        this.showHelpList(key,id,'more')
      },
      hideHelpList(){
        this.helpListShowIndex = null;
        this.noMoreHelp = true;
      },
      search(){
        if(this.searchWord == ''){
          alert('请输入与关键词')
        }else{
          var data = {
            start:0,
            limit:20,
            orgName:this.searchWord
          }
          axios.post(this.pc_proxy + 'queryDreams.do',qs.stringify(data)).then(res => {
            if (res.status == 200 && res.data.error_no == '0') {
              let data = res.data.data.datas
              this.inProgressData = data;
              if(data.length == 0){
                this.noContent = true
              }else{
                this.noContent = false;
              }
              this.loadingShow = false;
              this.searchPage = true;
              this.noMore = false;
            }
          })
        }
      },
      jump(href){
        location.href = href;
      }
    }
  }
</script>

