                  .hide-list{
                    font-size 28px
                    text-align right
                    line-height 84px
                  }
                }
              }
            }
          }
          .more{
            width 173px
            height 45px;
            border-radius 5px
            background $yellow
            margin 0 auto
            margin-top 30px
            font-size 25px
            line-height 45px
            color #000
            text-align center
            i{
              font-size 12px
              display inline-block
              line-height 45px
              transform rotate(-90deg)
            }
          }
        }
        .no-content-bg{
          background url("../../assets/shetuan_nomore_bg.png") no-repeat center center
          -webkit-background-size:  cover
          background-size:  cover
          width:294px
          height 275px
          margin 0 auto
        }
      }
      footer{
        width 1200px
        margin 0 auto
        padding-bottom 80px
        background-color: $body
        div{
          width 100%
          height 50px
          white-space nowrap
          overflow hidden
          text-align center
          color #000000
          font-size 20px
          position relative
          span{
            width 120px
            height 50px
            font-weight 600
            display block
            position absolute
            top 0
            bottom 0
            right 0
            left 0
            margin auto
            line-height 50px
            z-index: 9
            background-color $body
          }
          .line{
            width 100%
            height 2px
            border-bottom  2px dashed #000000
            position absolute
            top 0
            bottom 0
            margin-top auto
            margin-bottom auto
            z-index: 8
          }
        }
        p{
          margin-top 20px
          font-size 16px
          color #000
          padding 0 20px
        }
      }
      .loading{
        position fixed
        top 0
        bottom 0
        right 0
        left 0
        margin auto
        background #000000
        opacity .5
        z-index: 999
        img{
          width 100px
          height 100px
          position absolute
          top 0
          bottom 0
          right 0
          left 0
          margin auto
        }
      }
    }
  }
</style>



